import React from 'react'

export default function BarraDeNavegacao(props){
    const {logo} = props
    return (
        <div className="pl-1 bg-dark container" {...props}>
            <LinkButton location="" className="badge badge-light">
                <img src={logo} height="30" alt="IF Baiano"/>
                <label>Estágios GM</label>
            </LinkButton>
            <LinkButton location="requerimento" value="Requerimento" className="btn-warning" />
            <LinkButton location="lista_locais" value="Lista de Locais Cadastrados" className="btn-primary" />
            <LinkButton location="proposta_concedente" value="Propor Local para Estágios" className="btn-secondary" />
        </div>
    )
}

function LinkButton(props){
    const {location, className, value} = props
    const children = value === undefined ? props.children : value

    return <button {...props} className={`btn mr-1 ${className}`} onClick={(e)=> window.location = `#${location}`} children={children} />
}